import "./plugins/jquery.min.js";
import "./bootstrap/js/bootstrap.bundle.min.js";

const sectionMoveItem = document.querySelector("#next-page");

if (sectionMoveItem) {
  sectionMoveItem.addEventListener("click", function(e) {
    e.preventDefault();

    const nextPage = sectionMoveItem.getAttribute("href");

    document
      .querySelector(nextPage)
      .scrollIntoView({ behavior: "smooth", block: "start" });
  });
}

document.addEventListener("scroll", function() {
  const scrollPosition = window.scrollY;
  if (scrollPosition >= 100) {
    document.querySelector("header").classList.add("elem-dropdown");
  } else {
    document.querySelector("header").classList.remove("elem-dropdown");
  }
});
